import { APP } from "../../shared/app/APP";
import { titles as titlesEventos } from "../../shared/app/Titles";
import { GitHubRepository } from "../../shared/utils/git";
import { Evento } from "../../shared/app/Eventos";
import { reactive } from "vue";
import { EventSocket } from "./socket";

const database = {
  events: [] as Evento[],
  updated: null as Date | null,
};

export const app = <APP>reactive(new APP("APP", titlesEventos));

// Carregar dados
function load(eventos: Evento[]) {
  if (Array.isArray(eventos)) {
    app.eventos.values = [];
    eventos.map((e) => app.eventos.values.push(app.eventos.create(e)));
    app.sortEventos();
  }
}

const settings = {
  fetch: {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
    },
  },
};

fetch(`/data/database.json`, settings.fetch);
/**
 * É admin
 */
if (app.isAdmin && navigator.onLine) {
  const token = localStorage.getItem("key") || "";
  const ip_socket = "https://socket.goias.ifg.edu.br";
  const event = new EventSocket(ip_socket, token);

  const githubRepo = new GitHubRepository(
    "ccbfaina",
    "ccbfaina.github.io",
    token
  );

  event.onMessage((data) => {
    const { updated, events } = JSON.parse(data);
    const up = new Date(`${updated}`);
    database.updated = up;
    console.log("Carregados novos dados de ", up.toLocaleString());
    load(events);
  });

  event.onConnect(() => {
    console.log("Conectado no servidor socket: ", token);
    githubRepo.getContentData("data/database.json").then((res) => {
      if (res?.content) {
        event.toRoom("events", res.content);
      }
    });
  });

  app.onSaveAllEvents(() => {
    const lastTime = app.eventos.values.reduce((crr, e) => {
      const time = e?.updated?.getTime() ?? 0;
      return Math.max(crr, time);
    }, 0);

    database.updated = new Date(lastTime);
    database.events = app.eventos.values;

    const msg = JSON.stringify(database);

    event.toRoom("events", msg);
    githubRepo.commitFile("data/database.json", msg).then((result) => {
      if (result && result.ok) {
        console.log(
          `Dados atualizados (${app.eventos.values.length}) no Git com status ${result.status}`
        );
      }
    });
  });
} else {
  const fetchEvents = async (url: string) => {
    try {
      const response = await fetch(url);
      if (response.ok) {
        return await response.json();
      }
    } catch (error) {
      console.error(`Erro ao baixar ${url}:`, error);
    }
    return null;
  };

  const loadEvents = async (): Promise<void> => {
    try {
      const currentDateUrl = `/data/database.json?v=${new Date()
        .toISOString()
        .replace(/\D/g, "")}`;
      const { events, updated } =
        (await fetchEvents(currentDateUrl)) ||
        (await fetchEvents("/data/database.json"));

      if (events && updated) {
        database.events = events;
        database.updated = updated;
        load(database.events);
      } else {
        console.error("Erro ao carregar dados de cache.");
      }
    } catch (error) {
      console.error("Erro ao carregar os dados: ", error);
    }
  };

  loadEvents();
}
